<template>
  <div class="page">
    <Header>
      <a class="publish-btn" :class="{'active': commentContext}" href="javascript:;" slot="right" @click="toSubmit">提交</a>
    </Header>
    <div class="page-warp">
      <div class="answer-title">
        <img src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/goods/answer/question.png" alt="">
        <h2>{{getQuestionInfo.question}}</h2>
      </div>
      <div class="answer-detail">
        <div class="answer-item">
          <img :src="getReplayInfo.headImg || 'https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/goods/answer/user_default.png'" alt="">
          <div class="answer-item-body">
            <div class="answer-user">{{getReplayInfo.nickName}} <span :class="{'invite': getReplayInfo.identity==2}">{{userIdentity[getReplayInfo.identity]}}</span></div>
            <div class="answer-content">{{getReplayInfo.answer}}</div>
            <div class="answer-other">
              <p>{{getReplayInfo.createTime}}</p>
              <div @click="doLike">
                <span :class="{'active': getReplayInfo.recordId}"><i class="icon-zan"></i>{{getReplayInfo.likeCount}}</span>
              </div>
            </div>
          </div>
        </div>
        <div>
          <div class="answer-body" v-if="list.length">
            <h5>评论回复 </h5>
            <van-list
              class="answer-list"
                v-model="loading"
                :finished="finished"
                finished-text="没有更多了"
                @load="onLoad"
              >
              <div class="answer-item" v-for="item in list" :key="item.commentId">
                <img :src="item.headImg || 'https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/goods/answer/user_default.png'" alt="">
                <div class="answer-item-body">
                  <div class="answer-user">{{item.nickName}}<span :class="{'invite': item.identity==2}">{{userIdentity[item.identity]}}</span></div>
                  <div class="answer-replay" v-if="item.parentComment"><span>@{{item.parentComment.nickName}}：</span>{{item.parentComment.comment}}</div>
                  <div class="answer-content">{{item.comment}}</div>
                  <div class="answer-other">
                    <p>{{item.createTime}}</p>
                    <div>
                      <a href="javascript:;" @click="toReplay(item)">回复</a>
                    </div>
                  </div>
                </div>
              </div>
            </van-list>
          </div>
          <AnswerEmpty v-if="!list.length && finished"/>
        </div>
      </div>
    </div>
    <div class="fixed-input">
      <div>
        <span>{{commentToUser}}</span>
        <input v-model="commentContext" type="text" placeholder="请输入您的评论">
      </div>
      <a href="javascript:;" v-if="commentToUser" @click="cancelToReplay">取消</a>
    </div>
  </div>
</template>

<script>
import Header from '@/components/header'
import AnswerEmpty from './component/answer-empty'
import { getPageProductComment, addProductAnswerComment, addProductAnswerLikes } from '@/http/service/goods'
import { mapGetters, mapMutations } from 'vuex'
export default {
  name: 'goodstoreplay',
  components: {
    Header,
    AnswerEmpty,
  },
  data() {
    return {
      isActive: false,
      answerId: '',
      commentToUserId: '', // 评论回复对象id
      commentToUser: '', // 评论回复对象
      commentContext: '', // 评论内容
      curPage: 1,
      pageSize: 20,
      questionInfo: {},
      list: [],
      loading: false,
      finished: false,
      userIdentity: {
        1: '已买',
        2: '商家',
        3: '平台'
      }
    }
  },
  computed: {
    ...mapGetters('answerArea', ['getQuestionInfo', 'getReplayInfo']),
    ...mapGetters(['mId']),
  },
  mounted () {
    this.answerId = this.$route.params.id
    console.log(this.getReplayInfo)
    this.getPageProductComment()
  },
  methods: {
    ...mapMutations('answerArea', ['setReplayInfo']),
    init() {
      this.curPage = 1;
      this.list = []
      this.getPageProductComment()

      this.cancelToReplay()
      this.commentContext = ''
    },
    onLoad() {
      this.getPageProductComment()
    },
    getPageProductComment() {
      this.loading = true
      let params = {
        start: this.curPage,
        offset: this.pageSize,
        answerId: this.answerId,
      }
      getPageProductComment(params).then(res => {
        let { result, success } = res;
        if (success) {
          this.questionInfo =
          this.curPage += 1;
          this.loading = false
          this.list = [...this.list, ...result]
          this.finished = result.length < this.pageSize
        } else {
          this.loading = false
          this.finished = true
        }
      })
    },
    toSubmit() {
      if (!this.commentContext) {
        this.$toast('请输入评论内容');
        return;
      }
      let params = {
        answerId: this.answerId,
        commentUserId: this.mId,
        comment: this.commentContext,
        parentId: this.commentToUserId
      }
      addProductAnswerComment(params).then(res => {
        let { success } = res;
        if (success) {
          this.$toast('回复成功')
          this.init()
        } else {
          this.$toast('回复失败')
        }
      })
    },
    toReplay(item) {
      this.commentToUserId = item.commentId
      this.commentToUser = `回复 ${item.nickName} `
    },
    cancelToReplay() {
      this.commentToUserId = ''
      this.commentToUser = ''
    },
    doLike() {
      addProductAnswerLikes({
        answerId: this.answerId
      }).then(res => {
        let { success } = res;
        if (success) {
          if (this.getReplayInfo.recordId) {
            this.getReplayInfo.recordId = false
            this.getReplayInfo.likeCount -= 1
          } else {
            this.getReplayInfo.recordId = true
            this.getReplayInfo.likeCount += 1
          }
          this.setReplayInfo(this.getReplayInfo)
          // item.recordId = true
          // item.likeCount += 1
        }
      })
    }
  }
}
</script>

